.reviews__list {
  position: relative;
  margin: 0;
  padding: 41px 0 20px;
  list-style: none;
}

.reviews__item {
  display: none;
}

.reviews__item:first-child {
  display: block;
}

.reviews__button {
  display: none;
  border: none;
  padding: 0;
}

@media (min-width: $tablet-width) {
  .reviews__list {
    padding-top: 83px;
    padding-left: 0;
    padding-right: 0;
  }
}

@media (min-width: $desktop-width) {
  .reviews__wrapper {
    position: relative;
  }

  .reviews__list {
    padding-top: 174px;
    padding-bottom: 19px;

    &::before {
      content: "";
      position: absolute;
      top: 72px;
      left: 50%;
      transform: translateX(-50%);
      width: 79px;
      height: 63px;
      background-image: url("../img/icons/quotes.svg");
      background-repeat: no-repeat;
    }
  }

  .reviews__button {
    position: absolute;
    top: 52%;
    display: block;
    width: 22px;
    height: 41px;
    background-color: inherit;
    background-position: 0 0;

    &::before {
      content: "";
      position: absolute;
      top: 5px;
      width: 28px;
      height: 28px;
      border-top: 3px solid $dark;
      border-right: 3px solid $dark;
      opacity: 0.3;
    }

    &:hover::before {
      opacity: 0.6;
    }

    &:focus::before {
      opacity: 0.1;
    }

    &--prev {
      left: 3px;

      &::before {
        transform: rotate(-135deg);
        left: 3px;
      }
    }

    &--next {
      right: 2px;

      &::before {
        transform: rotate(45deg);
        right: 2px;
      }
    }
  }
}
